.question-card {
    width: 100vw;
    position: absolute;
    top: 0;
    bottom: 80px;
    left: 0;
}

.question-crad-title {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-weight: bold;
    color: var(--primary-color);
}

.question-crad-content {
    height: calc(100% - 60px);
    width: calc(100% - 20px);
    padding: 0 10px;
    overflow-y: auto;
}

.question-operate {
    height: 80px;
    width: 100vw;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    color: var(--primary-color);
    cursor: pointer;
}
.question-operate-item {
    width: 30%;
    text-align: center;
}